<template>
  <div id="app">
    <headerTop></headerTop>
    <div class="content">
      <div class="schedule">
        <div class="activity">
          老人带新人每人赠送<span class="pay">100</span>元现金。
        </div>
      </div>
      <div class="schedule">
        <div class="schedule-header">课程安排</div>
        <div class="schedule-content">
          <table class="familyTable" border="0" cellspacing="0" cellpadding="0">
            <thead>
              <tr>
                <th><span class="one">课程</span><div></div><span class="two">类别</span></th>
                <th>数学</th>
                <th>语文</th>
                <th>英语</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>小&nbsp;&nbsp;&nbsp;&nbsp;学</td>
                <td>20元 / 小时</td>
                <td>20元 / 小时</td>
                <td>20元 / 小时</td>
              </tr>
              <tr>
                <td>小升初</td>
                <td>30元 / 小时</td>
                <td>30元 / 小时</td>
                <td>30元 / 小时</td>
              </tr>
              <tr>
                <td>初&nbsp;&nbsp;&nbsp;&nbsp;中</td>
                <td>40元 / 小时</td>
                <td>40元 / 小时</td>
                <td>40元 / 小时</td>
              </tr>
            </tbody>
          </table>
          <div class="bonus">
            <h1>附赠</h1>
            <p>1、前20名报名的同学赠送教材。</p>
            <p>2、补三科的同学赠送午饭和托管。</p>
          </div>
          <!--<ul class="course">
            <li><span class="name">数学</span>
              <span class="text">
                <p>小&nbsp;&nbsp;&nbsp;&nbsp;学：20元 / 小时</p>
                <p>小升初：30元 / 小时</p>
                <p>初&nbsp;&nbsp;&nbsp;&nbsp;中：40元 / 小时</p>
              </span>
            </li>
            <li><span class="name">语文</span>
              <span class="text">
                <p>小&nbsp;&nbsp;&nbsp;&nbsp;学：20元 / 小时</p>
                <p>小升初：30元 / 小时</p>
                <p>初&nbsp;&nbsp;&nbsp;&nbsp;中：40元 / 小时</p>
              </span>
            </li>
            <li><span class="name">英语</span>
              <span class="text">
                <p>小&nbsp;&nbsp;&nbsp;&nbsp;学：20元 / 小时</p>
                <p>小升初：30元 / 小时</p>
                <p>初&nbsp;&nbsp;&nbsp;&nbsp;中：40元 / 小时</p>
              </span>
            </li>
          </ul>-->
        </div>
      </div>

      <div class="schedule">
        <div class="schedule-header">开心学习 &nbsp;<span>筑梦未来</span></div>
        <div class="schedule-content">
          <ul class="study">
            <li><span>1</span>学习存储基础知识</li>
            <li><span>2</span>提前掌握新课知识重点</li>
            <li><span>3</span>语数外主课重点讲解难点</li>
          </ul>
        </div>
      </div>
      <div class="schedule">
        <div class="schedule-header">未来星 · 寒假补课公告</div>
        <div class="schedule-content notice">
          学校开始放假的第一天就补课。为期<span>25</span>天。如果学校放假早，补课休周六周日（双休）。如果学校正常放假，补课休周日（单休）。
        </div>
      </div>
    </div>
    <bottomBox></bottomBox>
  </div>
</template>

<script>
  import headerTop from './components/header/header'
  import bottomBox from './components/bottom/bottom'
  import './assets/css/style.css'
  const axios = require('axios')
  export default {
    name: 'App',
    created () {
      axios.get('../static/datas/data.json')
        .then(function (response) {
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
        })
    },
    components: {
      headerTop,
      bottomBox
    }
  }
</script>

<style>
  .content{
    margin-top: -20px;
    width: 100%;
    display: inline-block;
  }
  .schedule{
    position: relative;
    width: 94%;
    margin-left: 3%;
    margin-bottom: 20px;
    display: inline-block;
  }
  .schedule-header{
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    background-color: #3a1a8b;
    color: #46aecd;
    letter-spacing:2px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }
  .schedule-header span{
    color: #fff;
  }
  .schedule-content{
    padding: 8px 6px;
    background-color: #fff;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
  }
  .notice{
    font-size: 14px;
    line-height: 22px;
    text-indent: 2em;
  }
  .notice > span{
    color: red;
    font-weight: bold;
    letter-spacing:2px;
    padding: 0 4px;
  }
  /*课程表*/
  .course{
    width: 100%;
    display: inline-block;
    font-size: 12px;
  }
  .course li{
    border: 1px solid #dcdcdc;
    display: flex;
  }
  .course li:not(:last-child){
    border-bottom: 0;
  }
  .course li span.name{
    flex: 0 0 50px;
    text-align: center;
    border-right: 1px solid #dcdcdc;
    font-weight: 700;
    line-height: 76px;
  }
  .course li span.text{
    flex: 1;
    padding: 8px 10px;
    line-height: 20px;
  }
  /*end*/
  .familyTable{
    width: 100%;
  }
  .familyTable thead tr th,.familyTable tbody tr td{
    border: 1px solid #dcdcdc;
    padding: 10px 5px;
    position: relative;
    font-size: 14px;
    text-align: center;
  }
  .familyTable thead tr th div{
    position: absolute;
    width: 78px;
    height: 1px;
    background-color: #dcdcdc;
    top: 16px;
    left: -3px;
    transform: rotate(25deg);
  }
  .familyTable thead tr th:nth-child(1){
    width: 58px;
  }
  .familyTable thead tr th, .familyTable tbody tr td:first-child{
    font-weight: 700;
  }
  .familyTable thead tr th span.one{
    position: absolute;
    right: 4px;
    top: 4px;
    font-size: 10px;
  }
  .familyTable thead tr th span.two{
    position: absolute;
    left: 4px;
    bottom: 4px;
    font-size: 10px;
  }
  .bonus{
    width: 100%;
    display: inline-block;
    line-height: 22px;
  }
  .bonus h1{
    font-weight: 700;
    margin: 8px 0 0 0;
    font-size: 14px;
  }
  .bonus p{
    font-size: 14px;
  }
  .activity{
    text-align: center;
    font-size: 14px;
    text-decoration: underline;
    color: #fff;
    animation: bounce-in 4s linear 2s infinite alternate;
  }
  .activity span{
    color: red;
  }
  .activity span.pay{
    display: inline-block;
    padding: 0 8px;
    font-size: 16px;
    font-weight: 700;
  }
  .study{
    width: 100%;
    display: inline-block;
    padding: 2px 0;
  }
  .study li{
    width: 100%;
    height: 40px;
    display: inline-block;
    background-color: #3810c4;
    border-radius: 40px;
    margin-bottom: 10px;
    font-size: 14px;
    color: #fff;
  }
  .study li:last-child{
    margin-bottom: 0;
  }
  .study li span{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: inline-block;
    background-color: #fff;
    color: #3810c4;
    margin: 10px;
    text-align: center;
    line-height: 20px;
    vertical-align: center;
  }
  @media only screen and (min-width: 300px) and (max-width: 320px) {
    .familyTable thead tr th div{
      width: 69px;
      top: 16px;
      left: -3px;
      transform: rotate(29deg);
    }
    .familyTable thead tr th:nth-child(1){
      width: 48px;
    }
    .familyTable thead tr th,.familyTable tbody tr td{
      font-size: 10px;
    }
    .bonus p, .study li{
      font-size: 12px;
    }
  }
  @media only screen and (min-width: 410px) and (max-width: 420px) {
    .familyTable thead tr th div{
      width: 88px;
      top: 16px;
      left: -3px;
      transform: rotate(22deg);
    }
    .familyTable thead tr th:nth-child(1){
      width: 69px;
    }
    .familyTable thead tr th,.familyTable tbody tr td{
      font-size: 14px;
    }
    .bonus p{
      font-size: 14px;
    }
  }

  @keyframes bounce-in {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }
</style>



