<template>
    <div class="wrappers-header">
      <div class="header-img">
        <img class="bj" src="../../assets/images/header.jpg"/>
        <img class="logo" src="../../assets/images/logo.png"/>
        <img class="hot" src="../../assets/images/hot1.png"/>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: ''
    }
</script>

<style scoped>
  .wrappers-header{
    width: 100%;
  }
  .header-img{
    position: relative;
    text-align: center;
    width: 100%;
    height: 0;
    padding-top: 100%;
  }
  .bj{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .logo{
    position: absolute;
    width: 40%;
    top: 30px;
    left: 10%;
    z-index: 9;
  }
  .hot{
    position: absolute;
    width: 80%;
    top: 76px;
    left: 10%;
    z-index: 9;
  }
</style>



